<template>
  <div class="main">
    <div class="border left ">
      <ul>
        <li>
          <div class="flex-center icon-header" >
            <img class="icon-header-img" width="80" src="./../assets/icon.jpg"/>
          </div>
        </li>
        <li @click="doIndex">首页</li>
        <li @click="doFriends">朋友圈</li>
        <li @click="doLogin">登陆</li>
        <li @click="doRegister">注册</li>
        <li>退出</li>
      </ul>
    </div>
    <div class="border right">
      <router-view/>
    </div>
    <!--login-->
    <template>
      <div v-show="!login.hide" class="wrap flex-center">

        <div class="login-body  flex-center">
          <img src="../assets/login/close.png" width="64" class="close" @click="login.hide=true">
          <div class="login-main flex-center-column">
            <div class="flex-start border_1 sef-input">
              <img class="icon-input"  width="20" src="../assets/login/user.png"/>
              <input type="text" title="用户名、邮箱" placeholder="用户名、邮箱"/>
            </div>
            <div class="flex-start border_1  sef-input ">
              <img class="icon-input" width="20" src="../assets/login/pwd.png"/>
              <input type="text" title="密码" placeholder="密码"/>
            </div>
            <div class="flex-center border_1 sef-input ">
              <button class="btn-login">登陆</button>
            </div>
            <div class="flex-start sef-input ot ">
              <span style="color: #406EBA">还没有账号? <a href="#">点击注册</a></span>
            </div>

          </div>
        </div>
      </div>
    </template>
    <!--register-->
    <template>
      <div v-show="!register.hide" class="wrap flex-center">
        <div class="login-body flex-center">
          <img src="../assets/login/close.png" width="64" class="close" @click="register.hide=true">
          <div class="login-main flex-center-column">
            <div class="flex-start border_1 sef-input">
              <img class="icon-input"  width="20" src="../assets/login/user.png"/>
              <input type="text" title="用户名、邮箱" placeholder="用户名、邮箱"/>
            </div>
            <div class="flex-start border_1 sef-input ">
              <img class="icon-input" width="20" src="../assets/login/pwd.png"/>
              <input type="text" title="密码" placeholder="密码"/>
            </div>
            <div class="flex-start border_1 sef-input ">
              <img class="icon-input" width="20" src="../assets/login/phone.png"/>
              <input type="text" title="手机" placeholder="手机"/>
            </div>
            <div class="flex-center border_1 sef-input ">
              <button class="btn-login">注册</button>
            </div>
            <div class="flex-start sef-input ot ">
              <span style="color: #406EBA"><a href="#">点击登陆</a></span>
            </div>

          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {
        login: {hide: true,},
        register:{hide: true,}
      }
    },
    methods: {
      doLogin() {
        this.login.hide = false;
      },
      doRegister(){
        this.register.hide = false;
      },
      doFriends(){
      this.$router.push({name:'list'})
      },
      doIndex(){
        this.$router.push({name:'index'})
      }

    }
  }
</script>

<style scoped>
  .main {
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    left: 0;
    box-sizing: border-box;
    border: 2px solid #cccc;
    border-radius: 5px;
    background-color: white;
    display: flex;
    align-items: center;
  }

  .flex-center {
    display: flex ;
    justify-content: center ;
    align-items: center ;
  }

  .flex-center-column {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .flex-start {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .border {
    border: 2px solid #7b7b7b;
    border-radius: 5px;
  }
  .border_1 {
    border: 2px solid #5370D8;
    border-radius: 5px;
  }

  .left {
    margin-left: 2px;
    width: 7%;
    height: 99%;
    background-color: #2c3e50;
  }

  .left > ul {
    box-sizing: border-box;
    overflow: hidden;
    padding: 0 2px;
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .left > ul > li {
    background-color: #fff2eb;
    margin-top: 1px;
    width: 100%;
    flex-grow: 1;
    color: #152650;
    font-family: Arial,serif;
    list-style: none;
    display: flex;
    border: 1px solid #152650;
    border-radius: 6px;
    justify-content: flex-start;
    align-items: center;
  }
  .left > ul > li:nth-child(1){
    padding: 10px;
    box-sizing: border-box;
    background-color: transparent;
  }

  .left > ul > li:hover {
    background-color: #004a00;
  }

  .wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .login-body {
    position: relative;
    width: 500px;
    height: 500px;
    background-color: white;
    border-radius: 5px;
  }

  .close {
    position: absolute;
    top: -30px;
    right: -60px;
  }

  .login-main {
    width: 80%;
    height: 90%;
  }

  .h100-w100 {
    width: 100%;
    height: 100%;
  }

  .sef-input {
    width: 100%;
    height:50px;
    margin-top: 20px;
  }
  .sef-input>input{
    border: none;
    outline: none;
    width: 100%;
    line-height: 40px;
  }


  .icon-input {
    box-sizing: border-box;
    padding: 8px;
    width: 50px;
    height: 50px;
  }

  .btn-login {
    width: 100%;
    height: 50px;
    background-color:#5370D8;
    border-radius: 5px;
    font-size: 25px;
    color: white;
  }
  .ot>span:hover,a:hover{
    color: yellow;
  }

  .icon-header {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    border: 2px solid #cccc;
    background-color:transparent !important;
  }

  .icon-header-img {
    border-radius: 50%;
  }

  .right {
    position: relative;
    width: 92%;
    height: 99%;
  }
</style>



