import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '/',
      redirect:'/home',
      meta:{name: '主页'}
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      children:[
        {
          path:'list',
          name:'list',
          component:() => import("@/components/friends/List.vue"),
          meta:{name: '朋友圈'}
        },
        {
          path:'index',
          name:'index',
          component:() => import("@/components/main/index.vue"),
          meta:{name: '首页'}
        },
      ]
    }
  ]
})



